import _ from 'lodash';
import api from 'utils/api'
function sortBy(arr) { //
  return _.sortBy(arr, (item) => {
    return item.sortId
  })
}

function getRoles(callback) { //
  api.get(`/role`).then(res => {
    if (callback) callback(res.data)
    return res.data;
  })
  .catch(err => {
    console.log(err)
  });
  return [];
}

function getUsers(callback) { //
  api.get(`/user`).then(res => {
    let params = {
      id:'',
      name:'',
      roleId:''
    }
    res.data.unshift(params);
    if (callback) callback(res.data)
    return res.data;
  })
  .catch(err => {
    console.log(err)
  });
  return [];
}

function getCompany(callback) { //
  api.get(`/company?page=0&size=1000`).then(res => {
    let params = {
      id:'',
      name:''
    }
    res.data.list.unshift(params);
    if (callback) callback(res.data)
    return res.data;
  })
  .catch(err => {
    console.log(err)
  });
  return [];
}

function getGoods(id, callback) { //
  if(!id) return;
  api.get(`/goods?companyId=${id}&page=0&size=1000`).then(res => {
    if (callback) callback(res.data)
    return res.data;
  })
  .catch(err => {
    console.log(err)
  });
  return [];
}

function addAll(arry){ //
  let param = {
    dKey:'',
    id:'',
    dValue:'',
  }
  arry.unshift(param);
  return arry;
}

let departmentList = sortBy(JSON.parse(sessionStorage.departmentList)) //
let doctorTitleList = sortBy(JSON.parse(sessionStorage.doctorTitleList)) //
let hosLevelList = sortBy(JSON.parse(sessionStorage.hosLevelList)) //
let propertyList = sortBy(JSON.parse(sessionStorage.propertyList)) //
let skillTagList = sortBy(JSON.parse(sessionStorage.skillTagList)) //
let subDepartmentList = sortBy(JSON.parse(sessionStorage.subDepartmentList)) //
let typeList = sortBy(JSON.parse(sessionStorage.typeList)) //
let paperTypeList = sortBy(JSON.parse(sessionStorage.paperTypeList)) //
let hopType = sortBy(JSON.parse(sessionStorage.hopType)) //
typeList = addAll(typeList);
departmentList = addAll(departmentList);
doctorTitleList = addAll(doctorTitleList);
hosLevelList = addAll(hosLevelList);
propertyList = addAll(propertyList);
skillTagList = addAll(skillTagList);
subDepartmentList = addAll(subDepartmentList);
paperTypeList = addAll(paperTypeList);
hopType = addAll(hopType);

let sexList = [ //
  { id: 1, dKey: 1, dValue: '' },
  { id: 0, dKey: 0, dValue: '' },
]
let stateList = [ //
  { id: 0, dKey: 0, dValue: '' },
  { id: -1, dKey: -1, dValue: '' },
]
let ipoList = [
  { id: '1234560', dKey: true, dValue: '' },
  { id: '1234567', dKey: false, dValue: '' },
]

export {
  departmentList,
  doctorTitleList,
  hosLevelList,
  propertyList,
  getRoles,
  getUsers,
  getCompany,
  getGoods,
  skillTagList,
  stateList,
  subDepartmentList,
  typeList,
  ipoList,
  hopType,
  sexList,
  paperTypeList
}
